import logo from "../../assests/logo.png"
import { useSelector } from 'react-redux'
import { useLogOutUserMutation } from '../../Redux/appApi'
import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import NavDropdown from 'react-bootstrap/NavDropdown';
import { LinkContainer } from 'react-router-bootstrap'
import { Button } from 'react-bootstrap'
import { useTheme } from "../../Theme/ThemeContext";
import { useNavigate } from "react-router-dom";
import 'flag-icon-css/css/flag-icons.min.css';
import i18next, { t } from "i18next";
import Cookies from "js-cookie";
import { useEffect } from "react";


export default function Navigation() {
    const user = useSelector((state: any) => state.user)


    const navigate = useNavigate()

    const { theme, toggleTheme } = useTheme();

    interface Languages {
        code: string,
        name: string,
        country_code: string,
        dir: string
    }


    const language: Languages[] = [
        {
            code: "en",
            name: "English",
            country_code: "us",
            dir: "ltr"

        },
        {
            code: "ar",
            name: "العربية",
            country_code: "eg",
            dir: "rtl"
        }
    ];


    const currentLanguagecode = Cookies.get('i18next') || "en"
    const currentLanguage = language.find(ele => ele.code === currentLanguagecode)

    useEffect(() => {
        document.body.dir = currentLanguage?.dir || 'ltr'
    }, [currentLanguage])

    const [LogOutUser] = useLogOutUserMutation()
    async function handleLogOut(e: any) {
        e.preventDefault()
        await LogOutUser(user)
        localStorage.setItem('protect', 'false')
        navigate("/Chat/Login")
    }

    const handleChangeLanguage = (languageCode: string) => {
        i18next.changeLanguage(languageCode).then(() => {
            navigate("/chat/Home");
        });
    }



    return (
        <>
            <Navbar expand="lg" bg={theme} data-bs-theme={theme}  >
                <Container>
                    <LinkContainer to="/Chat/Home">
                        <Navbar.Brand >
                            <img src={logo} alt="logo" className='logo' />
                        </Navbar.Brand>
                    </LinkContainer>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className={currentLanguagecode === "ar" ? "me-auto" : "ms-auto"}>
                            <div onClick={toggleTheme} style={{ cursor: "pointer" }} className="mt-1 me-2">
                                {theme === 'light' ?

                                    <svg viewBox="0 0 24 24" width="32" height="32" className="sun lightToggleIcon_SFTY"><path fill="currentColor" d="M12,9c1.65,0,3,1.35,3,3s-1.35,3-3,3s-3-1.35-3-3S10.35,9,12,9 M12,7c-2.76,0-5,2.24-5,5s2.24,5,5,5s5-2.24,5-5 S14.76,7,12,7L12,7z M2,13l2,0c0.55,0,1-0.45,1-1s-0.45-1-1-1l-2,0c-0.55,0-1,0.45-1,1S1.45,13,2,13z M20,13l2,0c0.55,0,1-0.45,1-1 s-0.45-1-1-1l-2,0c-0.55,0-1,0.45-1,1S19.45,13,20,13z M11,2v2c0,0.55,0.45,1,1,1s1-0.45,1-1V2c0-0.55-0.45-1-1-1S11,1.45,11,2z M11,20v2c0,0.55,0.45,1,1,1s1-0.45,1-1v-2c0-0.55-0.45-1-1-1C11.45,19,11,19.45,11,20z M5.99,4.58c-0.39-0.39-1.03-0.39-1.41,0 c-0.39,0.39-0.39,1.03,0,1.41l1.06,1.06c0.39,0.39,1.03,0.39,1.41,0s0.39-1.03,0-1.41L5.99,4.58z M18.36,16.95 c-0.39-0.39-1.03-0.39-1.41,0c-0.39,0.39-0.39,1.03,0,1.41l1.06,1.06c0.39,0.39,1.03,0.39,1.41,0c0.39-0.39,0.39-1.03,0-1.41 L18.36,16.95z M19.42,5.99c0.39-0.39,0.39-1.03,0-1.41c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06c-0.39,0.39-0.39,1.03,0,1.41 s1.03,0.39,1.41,0L19.42,5.99z M7.05,18.36c0.39-0.39,0.39-1.03,0-1.41c-0.39-0.39-1.03-0.39-1.41,0l-1.06,1.06 c-0.39,0.39-0.39,1.03,0,1.41s1.03,0.39,1.41,0L7.05,18.36z"></path></svg> :
                                    <svg viewBox="0 0 24 24" width="32" height="32" className="toggleIcon_oIOL darkToggleIcon_ekgs"><path fill="currentColor" d="M9.37,5.51C9.19,6.15,9.1,6.82,9.1,7.5c0,4.08,3.32,7.4,7.4,7.4c0.68,0,1.35-0.09,1.99-0.27C17.45,17.19,14.93,19,12,19 c-3.86,0-7-3.14-7-7C5,9.07,6.81,6.55,9.37,5.51z M12,3c-4.97,0-9,4.03-9,9s4.03,9,9,9s9-4.03,9-9c0-0.46-0.04-0.92-0.1-1.36 c-0.98,1.37-2.58,2.26-4.4,2.26c-2.98,0-5.4-2.42-5.4-5.4c0-1.81,0.89-3.42,2.26-4.4C12.92,3.04,12.46,3,12,3L12,3z"></path></svg>

                                }

                            </div>
                            {!user && (
                                <LinkContainer to="/Chat/Login">
                                    <Nav.Link >{t('Login.button')}</Nav.Link>
                                </LinkContainer>
                            )}
                            <LinkContainer to="/Chat">
                                <Nav.Link >{t('nav.chat')}</Nav.Link>
                            </LinkContainer>
                            {user && (
                                <NavDropdown title={
                                    <>
                                        <img src={user?.ProfilePic} style={{ width: 30, height: 30, marginRight: 10, objectFit: "cover" }} className=' rounded-circle' alt="" />
                                        {user?.name}
                                    </>
                                } id="basic-nav-dropdown">
                                    <LinkContainer to="/Chat/Change">
                                        <NavDropdown.Item className={theme === 'dark' ? 'text-light' : 'text-dark'}>{t('nav.change')}</NavDropdown.Item>
                                    </LinkContainer>

                                    <NavDropdown.Divider />
                                    <NavDropdown.Item>
                                        <Button variant="danger" onClick={handleLogOut}>
                                            {t('nav.logout')}
                                        </Button>
                                    </NavDropdown.Item>
                                </NavDropdown>
                            )}
                            <NavDropdown title={<i className="fa-solid fa-globe color-icon fs-5 mx-1"></i>} id="nav-dropdown">
                                {language?.map((ele: Languages, i: number) => (
                                    <NavDropdown.Item key={i}>
                                        <Button variant="none" className=" border-0" onClick={() => handleChangeLanguage(ele?.code)} disabled={ele?.code === currentLanguagecode}>
                                            <span className={`flag-icon  flag-icon-${ele?.country_code} mx-2`} ></span>
                                            {ele.name}
                                        </Button>
                                    </NavDropdown.Item>
                                ))}
                            </NavDropdown>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>
        </>

    )
}





